header{
	margin: 20px 10% 0 10%;
	height: 54px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	@include media-sm{
		margin: auto 10% auto 10%;
	}



	nav{
		display: flex;

		@include media-sm{
			width: 100%;
			justify-content: space-between;
		}

		.menu-item{
			@include media-sm{
				margin-right: 7px;
				font-size: 10px;

			}
			a{
				color: #252B42;
				text-decoration: none;
				img{
	
					margin-left: 12px;
				}
		
			}
			font-family: 'Inter';
			font-style: normal;
			font-weight: 500;
			font-size: 14px;
			line-height: 17px;
			/* identical to box height */

			text-align: center;

			color: #252B42;
			margin-right: 40px;

			@include media-sm{
				margin-right: 70px;
			}
			@include media-sm{
				margin-right: 10px;
            }
			
			.title img{
				@include media-sm{
					display: none;
				}
			}

			ul{
				display: none;
			}
		}
	}
	.button-block{
		height: 100%;
		display: flex;
		align-items: center;
		.phone-number{
			margin-right: 33px;
			.icon{
				margin-right: 12px;
			}
			font-family: 'Manrope';
			font-style: normal;
			font-weight: 500;
			font-size: 14px;
			line-height: 19px;
			text-align: center;
			
			color: #252B42;
		}
		button{
			width: 137px;
			height: 100%;
		}
		button.sign-up{
			background: #4F46BA;
			border-radius: 5px;
			border: 0;

			font-family: 'Inter';
			font-style: normal;
			font-weight: 500;
			font-size: 14px;
			line-height: 17px;
			/* identical to box height */
			text-align: center;
			color: #FFFFFF;
		}
		
		@include media-sm{
			display: none;
		}
	}
}