section.cost{
    margin-top: 179px;
    margin-bottom: 288px;
    position: relative;
    width: 85%;
    @include media-sm{
        margin-top: 207px;

        margin-bottom: 30px;
    }
    h2{
        /* h2 */
        margin: 0;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 40px;
        line-height: 50px;
        /* identical to box height, or 125% */

        letter-spacing: 0.2px;

        color: #252B42;
    }

    .description{
        @include media-sm{
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 20px;
            /* or 111% */
            
            letter-spacing: 0.2px;
            
            color: #737373;
        }
        /* paragraph */
        margin-top: 20px;

        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        /* or 143% */

        letter-spacing: 0.2px;

        color: #737373;
    }


    .programms{
        margin-top: 50px;
        display: flex;
        justify-content: space-between;

        @include media-sm{
            flex-direction: column;
        }
        .programm{

            @include media-xll{

                padding: 30px;
  
            }

            @include media-sm{
                width: 100%;
                padding: 40px;
                margin: 20px 0;
            }

            
            background-color: #FFFFFF;
            width: 33%;
            padding: 40px;
            .title{
               /* h2 */

                /* h2 */


       
            
                // @include media-sml{
                //     height: 270px;
                // }
        
                // @include media-sm{
                //     height: 410px;
                // }
    

                font-family: 'Montserrat';
                font-style: normal;
                font-weight: 700;
                font-size: 40px;
                line-height: 50px;
                /* identical to box height, or 125% */

                letter-spacing: 0.2px;

                color: #252B42;



   
                @include media-sm{
                    font-family: 'Montserrat';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 40px;
                    line-height: 50px;
                    /* identical to box height, or 125% */

                    letter-spacing: 0.2px;

                    color: #252B42;
                }

                                   
                @include media-md{
                    font-size: 28px;

                }
       
            }
            .cost{
                /* paragraph */
                margin-top: 28px;

                font-family: 'Montserrat';
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;
                /* identical to box height, or 143% */

                letter-spacing: 0.2px;

                color: #737373;
            }
            button{
                /* link */
                // width: 170px;
                margin-top: 28px;
                // @include media-sm{
                //     font-size: 12px;
                // }
        
                font-family: 'Montserrat';
                font-style: normal;
                font-weight: 700;
                font-size: 14px;
                line-height: 24px;
                /* or 171% */

                letter-spacing: 0.2px;

                color: #4F46BA;
                border: none;
                background-color: transparent;

                img{
                    margin-left:65px;
                    @include media-md{
                        margin-left:10px;
    
                    }
                }
            }

        }

    }

    .button{
        margin-top: 76px;
        background: #4F46BA;
        border-radius: 5px;

        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        text-align: center;

        color: #FFFFFF;
        width: 269px;
        height: 68px;
        border: none;

        @include media-sm{
            width: 100%;

        }
    }



   
}